import { useState, useEffect } from 'react';
import 'destyle.css';
import './styles/global.css'
import './styles/color.css'
import './App.css'
import Header from './components/Header/Header';
import Calendar from './components/Calendar/Calendar';
import Grade from './components/Grade/Grade';
import Curtain from './components/Curtain/Curtain.js'
import PDF from './PDF.js';

function App() {
  const GRADE_MAX = 6;
  // 子コンポーネントとstateのやり取りをするための関数
  const [year, setYear] = useState('');
  const [month, setMonth] = useState('');
  const [holidays, setHolidays] = useState([]);
  const [days, setDays] = useState([]);
  const [grade, setGrade] = useState(JSON.parse(localStorage.getItem('grade')) ?? [3,3,3]);
  const updateGrade = (gradeNum, classNum) => {
    const newGrade = grade.map((v, i) => {
      return gradeNum === i + 1 ? classNum : v;
    })
    setGrade(newGrade);
  }
  const deleteGrade = () => {
    setGrade((grade) => grade.slice(0, -1));
  }
  const addGrade = () => {
    setGrade((grade) => [...grade, 3]);
  }

  const generatePDF = () => {
    PDF.generate({
      year: year,
      month: month,
      holidays: holidays,
      grade: grade
    })
  }

  useEffect(() => {
    localStorage.setItem('grade', JSON.stringify(grade));
  })

  return (
    <div class='container'>
      <Header />
      <main>
        <div class='editor bgcolor-4'>
          <div class='calendar_wrap'>
            <Calendar
              setYear={ setYear }
              setMonth={ setMonth }
              setDays={ setDays }
              setHolidays={ setHolidays }
              generatePDF={ generatePDF }
            />
          </div>
          <div class='grade_wrap'>
            <Grade
              grade = { grade }
              updateGrade = { updateGrade }
              deleteGrade = { deleteGrade }
            />
            <p class={`addBtn ${grade.length < GRADE_MAX ? 'enabled' : ''}`} onClick={ addGrade }>+</p>
          </div>
        </div>
        <iframe title='PDFview' class='PDFview' id='pdfResult' src='#'></iframe>
      </main>
      <Curtain />
    </div>
  );
};

export default App;
