import { useState } from 'react';
import './Grade.css';

const GradeLabel = ({ grade, gradeNum, isLast, updateGrade, deleteGrade, classMin, classMax}) => {
  const CLASS_MIN = classMin ?? 0;
  const CLASS_MAX = classMax ?? 9;
  
  const [classNum, setClassNum] = useState(grade[gradeNum - 1]);

  return (
    <div class='grade'>
      <div class='gradeNum_wrap'>
        <h2>{`${ gradeNum }`}年</h2>
        <div class='classNum_wrap'>
          <select name={`grade${ gradeNum }`} id={`grade${ gradeNum }`} value={ classNum } onChange={ (e)=>{
            updateGrade(gradeNum, parseInt(e.target.value))
            setClassNum(parseInt(e.target.value));
          }}>
          {[...Array(CLASS_MAX - CLASS_MIN + 1)].map((v, i) => (
            <option value={`${i + CLASS_MIN}`}>{`${i + CLASS_MIN}`}</option>
          ))}
          </select>
          <h2>
            クラス
          </h2>
        </div>
      </div>
      <p class={`deleteBtn ${isLast && grade.length > 1? 'enabled' : ''}`} onClick={ deleteGrade }></p>
    </div>
  )
}

export default GradeLabel;