import { useState } from 'react';
import './Curtain.css';

const Curtain = () => {
  const [isHide, setIsHide] = useState(false);
  const hide = () => {
    setIsHide(true);
  }
  return (
    <div class={`curtain ${isHide ? 'hide' : '' }`} role='button' tabIndex={1} onClick={hide}>
      <p>※本サイトではパソコンでの閲覧を推奨しています。※</p>
      <p>（レスポンシブ対応がめんどくさかったなんて口が滑っても言えない）</p>
    </div>
  )
}

export default Curtain;