import React, { useState, useEffect } from 'react';
import JapaneseHolidays from 'japanese-holidays';
import './Calendar.css'; // スタイリング用のCSSファイルをインポート

// 現在の月の日付を取得
const getDays = (year, month) => {
  const daysInMonth = new Date(year, month, 0).getDate();
  const days = [];
  for (let i = 1; i <= daysInMonth; i++) {
    days.push(i);
  }
  return days;
};

// 休日を取得
const getHolidays = (year, month) => {
  const daysInMonth = new Date(year, month, 0).getDate();
  const holidays = [];
  for (let i = 1; i <= daysInMonth; i++) {
    const date = new Date(year, month-1, i);
    const day = date.getDay();
    if (JapaneseHolidays.isHoliday(date) || day == 0 || day == 6) {
      holidays.push(i);
    }
  }
  return holidays;
}

const MONTH = new Date().getMonth() == 11 ? 1 : new Date().getMonth() + 2;
const YEAR = MONTH == 1 ? new Date().getFullYear() + 1 : new Date().getFullYear();
const DAYS = getDays(YEAR, MONTH);
const HOLIDAYS = getHolidays(YEAR, MONTH);

const Calendar = (props) => {
  // PDFを作成する関数
  const generatePDF = () => {props.generatePDF()};

  // 親コンポーネントにstateを送る関数
  const setParentYear = (year) => {props.setYear(year)};
  const setParentMonth = (month) => {props.setMonth(month)};
  const setParentDays = (days) => {props.setDays(days)};
  const setParentHolidays = (holidays) => {props.setHolidays(holidays)};
  
  // 年、月、日付、休日
  const [year, setYear] = useState(YEAR);
  const [month, setMonth] = useState(MONTH);
  const [days, setDays] = useState(DAYS);
  const [holidays, setHolidays] = useState(HOLIDAYS);

  useEffect(() => {
    setParentYear(year);
    setParentMonth(month);
    setParentDays(days);
    setParentHolidays(holidays);
    generatePDF();
  })

  // 描画用の日付配列を作成
  const generateCalendarForDraw = () => {
    const date1 = new Date(year, month-1, 1);
    const day1 = date1.getDay();
    let new_days = days.concat();
    for (let i=0; i<day1; i++) {
      new_days.unshift(0);
    }
    while (new_days.length % 7) {
      new_days.push(0);
    }
    return new_days;
  }

  const handleDateClick = (date) => {
    setHolidays((prev) => 
      prev.includes(date) ? prev.filter((d) => d !== date) : [...prev, date]
    );
  };

  const handleArrowClick = (dmonth) => {
    let newYear = year;
    let newMonth = month + dmonth;
    switch (newMonth) {
      case 0:
        newYear -= 1;
        newMonth = 12;
        break;
      case 13:
        newYear += 1;
        newMonth = 1;
        break;
    }
    setYear(newYear);
    setMonth(newMonth);
    setDays(() => getDays(newYear, newMonth));
    setHolidays(() => getHolidays(newYear, newMonth));
  }

  return (
    <div class='calendar'>
      <div class='header'>
        <span onClick={() => {handleArrowClick(-1)}}>←</span>
        <h2>{`${year}年${month}月`}</h2>
        <span onClick={() => {handleArrowClick(1)}}>→</span>
      </div>
      <div class='calendar-grid'>
        {generateCalendarForDraw().map((date) => (
          <div
            key={date == 0 ? undefined : date}
            class={`calendar-cell ${holidays.includes(date) ? 'selected' : ''} ${date == 0 ? 'disabled' : ''} bgcolor-4`}
            onClick={date == 0 ? undefined : () => {
              handleDateClick(date);
            }}
          >
            {date == 0 ? '' : date}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Calendar;
