import './Grade.css';
import GradeLabel from './GradeLabel';

const Grade = ({ grade, updateGrade, deleteGrade }) => {
  return (
    grade.map((v, i) => {
      const gradeNum = i + 1;
      const isLast = gradeNum == grade.length ? true : false;
      return (
        <GradeLabel
          gradeNum = { gradeNum }
          isLast = { isLast }
          grade = { grade }
          updateGrade = { updateGrade }
          deleteGrade = { deleteGrade }
        />
      )
    })
  )
}

export default Grade;