const xhr = new XMLHttpRequest()

// 出力先
const IFRAME_ID = 'pdfResult';

class Error {
  zeroUnderError (name, value) {
    if (value < 0) {
      console.error(`${String(name)}'s ${String(value)} is under Zero`)
      return 'error'
    }
  }
  coordinateIsNotExistError (coordinate = {x: 0, y: 0}, field) {
    if ((0 <= coordinate.y && coordinate.y < field.length) && (0 <= coordinate.x && coordinate.x < field[0].length)) {
      return
    }
    console.error(`(${coordinate.x}, ${coordinate.y}) is not exist in this field.`)
    return 'error'
  }
  checkTable (tableData) {
    if (tableData => tableData.every(value => value.length === tableData[0].length)) {
      return
    }
    console.error('This table is disable.')
    return 'error'
  }
}

class PDF {
  constructor () {
    // pdfを初期化
    this.doc = new window.PDFDocument({
      size: 'A4',
      margin: 0
    })
    // 線のスタイルを初期化
    this.thinLineWidth = 1
    this.thickLineWidth = 2
    this.doubleLineWidth = 1
    this.doubleGap = 1
    // フォントについて
    this.fontHeightAspect = 1.5 // <--フォントの幅に対する高さの割合
  }

  // 線を描画する
  drawLine ({start = {x: 0, y: 0}, end = {x: 0, y: 0}, lineWidth = 1, color = '#000'}) {
    // 例外を検知
    if (
      new Error().zeroUnderError(start.x) === 'error' ||
      new Error().zeroUnderError(start.y) === 'error' ||
      new Error().zeroUnderError(end.x) === 'error' ||
      new Error().zeroUnderError(end.y) === 'error' ||
      new Error().zeroUnderError(lineWidth) === 'error'
    ) {
      return
    }

    // 表作成時に切り欠きが生じないようにする
    const startxToEndx = end.x - start.x
    const startyToEndy = end.y - start.y
    // 二点間の距離
    const startToEnd = Math.sqrt(startxToEndx ** 2 + startyToEndy ** 2)
    // 三角形は相似だから
    const startX = start.x - lineWidth / 2 * (startxToEndx / startToEnd)
    const startY = start.y - lineWidth / 2 * (startyToEndy / startToEnd)
    const endX = end.x + lineWidth / 2 * (startxToEndx / startToEnd)
    const endY = end.y + lineWidth / 2 * (startyToEndy / startToEnd)

    // 線を描画
    this.doc.save()
      .lineWidth(lineWidth)
      .moveTo(startX, startY)
      .lineTo(endX, endY)
      .stroke(color)
      .closePath()
  }

  // 細線(thin)を描画する
  drawThin ({start = {x: 0, y: 0}, end = {x: 0, y: 0}, color = '#000'}) {
    this.drawLine({
      start: start,
      end: end,
      lineWidth: this.thinLineWidth,
      color: color
    })
  }

  // 太線(thick)を描画する
  drawThick ({start = {x: 0, y: 0}, end = {x: 0, y: 0}, color = '#000'}) {
    this.drawLine({
      start: start,
      end: end,
      lineWidth: this.thickLineWidth,
      color: color
    })
  }

  // 二重線(double)を描画する
  drawDouble ({start = {x: 0, y: 0}, end = {x: 0, y: 0}, color = '#000'}) {
    // 斜めでも対応できるようにする
    const startxToEndx = end.x - start.x
    const startyToEndy = end.y - start.y
    // 二点間の距離
    const startToEnd = Math.sqrt(startxToEndx ** 2 + startyToEndy ** 2)
    // 基準の線分から二重線の中心までの距離
    const lineGap = (this.doubleLineWidth + this.doubleGap) / 2
    this.drawLine({
      start: {x: start.x - lineGap * (end.y - start.y) / startToEnd, y: start.y + lineGap * (end.x - start.x) / startToEnd},
      end: {x: end.x - lineGap * (end.y - start.y) / startToEnd, y: end.y + lineGap * (end.x - start.x) / startToEnd},
      lineWidth: this.doubleLineWidth
    })
    this.drawLine({
      start: {x: start.x + lineGap * (end.y - start.y) / startToEnd, y: start.y - lineGap * (end.x - start.x) / startToEnd},
      end: {x: end.x + lineGap * (end.y - start.y) / startToEnd, y: end.y - lineGap * (end.x - start.x) / startToEnd},
      lineWidth: this.doubleLineWidth
    })
  }

  // テキストを描画
  drawText ({text = '', start = {x: 0, y: 0}, end = {x: 0, y: 0}, fontSize = 'auto', horizontal = 'center', vertical = 'center', font = 'Times-Roman'}) {
    // 例外を検知
    if (
      new Error().zeroUnderError(start.x) === 'error' ||
      new Error().zeroUnderError(start.y) === 'error' ||
      new Error().zeroUnderError(end.x) === 'error' ||
      new Error().zeroUnderError(end.y) === 'error' ||
      new Error().zeroUnderError(fontSize) === 'error'
    ) {
      return
    }
    
    // フォントのデータ
    // autoの時
    if (fontSize === 'auto') {
      const cell = {height: Math.abs(start.y - end.y), width: Math.abs(start.x - end.x)}
      fontSize = Math.min(cell.height / this.fontHeightAspect, cell.width / text.length)
    }
    const fontWidth = fontSize
    const fontHeight = fontWidth * this.fontHeightAspect

    // 描画開始座標を計算
    let place = {x: 0, y: 0}

    // 水平方向のレイアウト-->place.xを定める
    switch (horizontal) {
      // 左揃え
      case 'left':
        place.x = start.x
        break
      // 右揃え
      case 'right':
        place.x = end.x - fontWidth * text.length
        break
      // 中央揃え
      default:
        place.x = (start.x + end.x - fontWidth * text.length) / 2
    }

    // 縦方向のレイアウト-->place.yを定める
    switch (vertical) {
      // 上揃え
      case 'top':
        place.y = start.y
        break
      // 下揃え
      case 'bottom':
        place.y = end.y - fontHeight
        break
      // 中央揃え
      default:
        place.y = (start.y + end.y - fontHeight) / 2
    }
    
    // 一文字ずつ描画
    for (let i = 0; i < text.length; i++) {
      // 文字を描画
      this.doc.fillColor('#000')
      this.doc.save()
        .fontSize(fontSize)
        .font(font)
        .text(text[i], place.x + fontWidth * i, place.y, {
          width: fontWidth,
          align: 'center'
        })
        .closePath()
    }
  }

  drawBackground ({start = {x: 0, y: 0}, end = {x: 0, y: 0}, color = '#d0d0d0'}) {
    this.doc.fillColor(color)
    this.doc.save()
      .moveTo(start.x, start.y)
      .lineTo(start.x, end.y)
      .lineTo(end.x, end.y)
      .lineTo(end.x, start.y)
      .lineTo(start.x, start.y)
      .fill()
      .closePath()
  }

  // 表を描画する
  drawTable ({tableData, start = {x: 0, y: 0}, end = {x: 0, y: 0}}) {
    // 表のデータをチェック
    if (new Error().checkTable(tableData) === 'error') {
      return
    }

    // 座標をチェック
    if (start.x > end.x || start.y > end.y) {
      console.error('This coordinate is disable.')
      return
    }

    // 表の情報を計算
    const table = {height: end.y - start.y, width: end.x - start.x}
    const tableNum = {vertical: tableData.length, horizontal: tableData[0].length}
    // セルの情報を計算
    const cell = {height: table.height / tableNum.vertical, width: table.width / tableNum.horizontal}

    // 表を描画
    for (let y = 0; y < tableData.length; y++) {
      for (let x = 0; x < tableData[y].length; x++) {
        const nowCell = tableData[y][x]
        const text = nowCell.text
        const fontSize = nowCell.fontSize
        const font = nowCell.font
        // そのまま読み込むとエラーが起きる
        let horizontal
        let vertical
        try {
          horizontal = nowCell.style.horizontal
        } catch (error) {
          horizontal = undefined
        }
        try {
          vertical = nowCell.style.vertical
        } catch (error) {
          vertical = undefined
        }

        // セルの描画開始座標
        const drawStart = {
          x: start.x + cell.width * x,
          y: start.y + cell.height * y
        }
        const drawEnd = {
          x: drawStart.x + cell.width,
          y: drawStart.y + cell.height
        }

        
        // 背景色を描画
        if (nowCell.background !== undefined) {
          this.drawBackground({
            start: drawStart,
            end: drawEnd,
            color: nowCell.background
          })
        }

        // テキストを描画
        this.drawText({
          text: text,
          start: drawStart,
          end: drawEnd,
          fontSize: fontSize,
          horizontal: horizontal,
          vertical: vertical,
          font: font
        })

        // 線を描画
        if (nowCell.border !== undefined) {
          // 左
          if (nowCell.border.left !== undefined && nowCell.border.left !== 'none') {
            const linePlace = {
              start: drawStart,
              end: {x: drawStart.x, y: drawStart.y + cell.height}
            }
            switch (nowCell.border.left) {
              case 'double':
                this.drawDouble(linePlace)
                break
              case 'thick':
                this.drawThick(linePlace)
                break
              default:
                this.drawThin(linePlace)
            }
          }
          // 右 --> 最後の列の時だけ実行
          if (x === tableNum.horizontal - 1 && nowCell.border.right !== undefined && nowCell.border.right !== 'none') {
            const linePlace = {
              start: {x: drawEnd.x, y: drawEnd.y - cell.height},
              end: drawEnd
            }
            switch (nowCell.border.right) {
              case 'double':
                this.drawDouble(linePlace)
                break
              case 'thick':
                this.drawThick(linePlace)
                break
              default:
                this.drawThin(linePlace)
            }
          }
          // 上
          if (nowCell.border.top !== undefined && nowCell.border.top !== 'none') {
            const linePlace = {
              start: drawStart,
              end: {x: drawStart.x + cell.width, y: drawStart.y}
            }
            switch (nowCell.border.top) {
              case 'double':
                this.drawDouble(linePlace)
                break
              case 'thick':
                this.drawThick(linePlace)
                break
              default:
                this.drawThin(linePlace)
            }
          }
          // 下 --> 最後の行の時だけ実行
          if (y === tableData.length - 1 && nowCell.border.bottom !== undefined && nowCell.border.bottom !== 'none') {
            const linePlace = {
              start: {x: drawEnd.x - cell.width, y: drawEnd.y},
              end: drawEnd
            }
            switch (nowCell.border.bottom) {
              case 'double':
                this.drawDouble(linePlace)
                break
              case 'thick':
                this.drawThick(linePlace)
                break
              default:
                this.drawThin(linePlace)
            }
          }
          // 斜線
          if (nowCell.border.diagonal !== undefined && nowCell.border.diagonal !== 'none') {
            const linePlace = {
              start: drawStart,
              end: drawEnd
            }
            switch (nowCell.border.diagonal) {
              case 'double':
                this.drawDouble(linePlace)
                break
              case 'thick':
                this.drawThick(linePlace)
                break
              default:
                this.drawThin(linePlace)
            }
          }
        }
      }
    }
  }

  // iframeにpdfを渡す
  end () {
    this.doc.end()
    // pdfを書き込み可能なオブジェクトに関連付ける
    const stream = this.doc.pipe(window.blobStream())
    let url = '';
    stream.on('finish', function() {
      url = stream.toBlobURL('application/pdf');
    })
    try {
      if (document.readyState === "loading") {
        // Loading hasn't finished yet
        document.addEventListener("DOMContentLoaded", () => {
          const iframe = document.getElementById(IFRAME_ID);
          iframe.src = url;
        });
      } else {
        // pdfを書き込み可能なオブジェクトに関連付ける
        const stream = this.doc.pipe(window.blobStream())
        stream.on('finish', function() {
          const iframe = document.getElementById(IFRAME_ID);
          iframe.src = url;
        })
      }
    } catch(e) {
      console.error(e);
    }
  }
}

class Table {
  constructor (rowNum, columnNum) {
    // 空の表のデータを作成
    this.field = [...Array(rowNum)].map(value => [...Array(columnNum)].map(value => new Object()))
  }
  // 指定した座標にテキストを設定
  setText (text, x, y) {
    if (new Error().coordinateIsNotExistError({x: x, y: y}, this.field) === 'error') {
      return
    }
    this.field[y][x].text = String(text)
  }
  // 指定した座標にborderオブジェクトを追加
  createBorder (x, y) {
    if (this.field[y][x].border === undefined) {
      this.field[y][x].border = new Object()
    }
  }
  // 指定した座標の線のスタイルを設定 --> none 無し, thin 細い, thick 太い, double 二重線 のみ
  setBorder (x, y, style = 'thin', place = 'top') {
    if (new Error().coordinateIsNotExistError({x: x, y: y}, this.field) === 'error') {
      return
    }
    this.createBorder(x, y)
    switch (place) {
      case 'left':
        if (x !== 0) {
          this.createBorder(x - 1, y)
          this.field[y][x - 1].border.right = style
        }
        this.field[y][x].border.left = style
        break
      case 'right':
        if (x !== this.field[0].length - 1) {
          this.createBorder(x + 1, y)
          this.field[y][x + 1].border.left = style
        }
        this.field[y][x].border.right = style
        break
      case 'bottom':
        if (y !== this.field.length - 1) {
          this.createBorder(x, y + 1)
          this.field[y + 1][x].border.top = style
        }
        this.field[y][x].border.bottom = style
        break
      case 'diagonal':
        this.field[y][x].border.diagonal = style
        break
      default:
        if (y !== 0) {
          this.createBorder(x, y - 1)
          this.field[y - 1][x].border.bottom = style
        }
        this.field[y][x].border.top = style
    }
  }
  // 指定した座標の水平方向のスタイルを設定 --> left 左寄せ, center 中央寄せ, right 右寄せ
  setTextHorizontalStyle (x, y, style = 'center') {
    if (new Error().coordinateIsNotExistError({x: x, y: y}, this.field) === 'error') {
      return
    }
    if (this.field[y][x].style === undefined) {
      this.field[y][x].style = new Object()
    }
    switch (style) {
      case 'left':
        this.field[y][x].style.horizontal = 'left'
        return
      case 'right':
        this.field[y][x].style.horizontal = 'right'
        return
      default:
        this.field[y][x].style.horizontal = 'center'
    }
  }
  // 指定した座標の縦方向のスタイルを設定 --> top 上, center 中央, bottom 下
  setTextVerticalStyle (x, y, style = 'center') {
    if (new Error().coordinateIsNotExistError({x: x, y: y}, this.field) === 'error') {
      return
    }
    if (this.field[y][x].style === undefined) {
      this.field[y][x].style = new Object()
    }
    switch (style) {
      case 'top':
        this.field[y][x].style.vertical = 'top'
        return
      case 'bottom':
        this.field[y][x].style.vertical = 'bottom'
        return
      default:
        this.field[y][x].style.vertical = 'center'
    }
  }
  // 指定した座標のフォントサイズを設定
  setFontSize (x, y, fontSize = 10) {
    if (isNaN(parseInt(fontSize))) {
      console.error(`${String(fontSize)} is cannot be fontSize.`)
      return 'error'
    }
    this.field[y][x].fontSize = parseInt(fontSize)
  }
  // 指定した座標のフォントを設定
  setFont (x, y, font) {
    this.field[y][x].font = font
  }
  // 指定した座標の背景色を設定
  setBackground (x, y, color = '#d0d0d0') {
    this.field[y][x].background = color
  }
}

// 遅刻者チェック表を作る
class NoChikoku {
  constructor ({
    year = 2000,
    month = 2,
    holidays = [1,2,3,4,5,6,27,29],
    grade = [3,2,3],
    margin = {top: 20, left: 20, right: 20, bottom: 20},
    titleHeightAspect = 1.3, // <-- セルの高さに対する割合
    subTitleHeightAspect = 0.8, // <-- セルの高さに対する割合
    gapAspect = 0.5, // <-- セルの高さに対する割合
    cellAspect = 1 // <-- セルの縦割る横
  }) {
    this.year = year
    this.month = month
    this.date = new Date(this.year, this.month, 0).getDate()
    this.holidays = holidays
    this.grade = grade
    this.margin = margin
    this.titleHeightAspect = titleHeightAspect
    this.subTitleHeightAspect = subTitleHeightAspect
    this.gapAspect = gapAspect
    this.cellAspect = cellAspect
    this.fontHeightAspect = 1.5
  }
  // 表を描画
  draw (font) {
    // 表に関する変数を定義
    const noChikokuDoc = new PDF()
    let table = {
      vertical: this.grade.reduce((sum, element) => {
        if (typeof sum !== 'number') {
          sum = 0
        }
        if (typeof element !== 'number') {
          element = 0
        }
        return sum + element
      }) + 1,
      horizontal: this.date,
      height: noChikokuDoc.doc.page.height - this.margin.top - this.margin.bottom,
      width: noChikokuDoc.doc.page.width - this.margin.left - this.margin.right,
      paragraph: undefined
    }
    let cuttedTable = {
      height: undefined,
      width: table.width,
      vertical: table.vertical,
      horizontal: undefined
    }
    let cell = {
      height: undefined,
      width: undefined,
      aspect: Infinity
    }
    let _cuttedTableHorizontal
    let _cellHeight
    let _cellWidth
    let _cellAspect
    for (let paragraph = 1; paragraph < 100; paragraph++) {
      _cuttedTableHorizontal = Math.ceil(table.horizontal / paragraph) + 1 // <-- 左側のクラス記入欄分のスペース
      _cellHeight = table.height / (this.titleHeightAspect + this.subTitleHeightAspect + cuttedTable.vertical * paragraph + this.gapAspect * (paragraph + 1))
      _cellWidth = cuttedTable.width / _cuttedTableHorizontal
      // アスペクト比を比較
      _cellAspect = _cellHeight / _cellWidth
      if (Math.abs(this.cellAspect - _cellAspect) < Math.abs(this.cellAspect - cell.aspect)) {
        cuttedTable.horizontal = _cuttedTableHorizontal
        cell.height = _cellHeight
        cell.width = _cellWidth
        cell.aspect = _cellAspect
        table.paragraph = paragraph
        continue
      } else {
        break
      }
    }
    cuttedTable.height = cell.height * cuttedTable.vertical
    const dayFontSize = Math.min(cell.height / this.fontHeightAspect, cell.width / `${String(this.date)}日`.length)

    // 表のデータを作成
    let noChikokuTable = []
    let date = 1
    for (let i = 0; i < table.paragraph; i++) {
      noChikokuTable[i] = new Table(cuttedTable.vertical, cuttedTable.horizontal)
      // 表全体に細線を設定
      for (let x = 0; x < cuttedTable.horizontal; x++) {
        for (let y = 0; y < cuttedTable.vertical; y++) {
          // 背景色
          if (this.holidays.indexOf(date) !== -1 && x > 0) {
            noChikokuTable[i].setBackground(x, y, '#d0d0d0')
          }
          noChikokuTable[i].setBorder(x, y, 'thin', 'top')
          noChikokuTable[i].setBorder(x, y, 'thin', 'left')
          noChikokuTable[i].setBorder(x, y, 'thin', 'right')
          noChikokuTable[i].setBorder(x, y, 'thin', 'bottom')
          // 外側の時太線
          switch (y) {
            case 0:
              noChikokuTable[i].setBorder(x, y, 'thick', 'top')
              break
            case cuttedTable.vertical - 1:
              noChikokuTable[i].setBorder(x, y, 'thick', 'bottom')
              break
          }
          switch (x) {
            case 0:
              noChikokuTable[i].setBorder(x, y, 'thick', 'left')
              break
            case cuttedTable.horizontal - 1:
              noChikokuTable[i].setBorder(x, y, 'thick', 'right')
              break
          }
          if (date === this.date) {
            noChikokuTable[i].setBorder(x, y, 'thick', 'right')
          }
          // (0, 0)の斜線
          if (x === 0 && y === 0) {
            noChikokuTable[i].setBorder(x, y, 'thin', 'diagonal')
          }
          // 一列目の時二重線
          if (x === 1) {
            noChikokuTable[i].setBorder(1, y, 'double', 'left')
          }
          // 一行目の時太線
          if (y === 1) {
            noChikokuTable[i].setBorder(x, 1, 'thick', 'top')
          }
          // 日付を設定
          if (y === 0 && x > 0) {
            noChikokuTable[i].setText(`${String(date)}日`, x, y)
            noChikokuTable[i].setFont(x, y, font)
            noChikokuTable[i].setFontSize(x, y, dayFontSize)
          }
        }
        if (x !== 0) {
          date++
          if (date > this.date) {
            break
          }
        }
      }
    }
    // 学年を設定
    date = 1
    for (let i = 0; i < table.paragraph; i++) {
      let y = 1
      for (let grade = 1; grade <= this.grade.length; grade++) {
        const nowClassNum = this.grade[grade - 1]
        // 学年のデータがなければループを外れる
        if (typeof nowClassNum !== 'number') {
          continue
        }
        // 学年名
        for (let classNum = 1; classNum <= nowClassNum; classNum++) {
          noChikokuTable[i].setText(`${String(grade)}-${String(classNum)}`, 0, y)
          noChikokuTable[i].setFont(0, y, font)
          y++
        }
        // 学年を分ける太線
        for (let x = 0; x < cuttedTable.horizontal; x++) {
          if (x >= this.date + (i + 1) - cuttedTable.horizontal * i) {
            break
          }
          noChikokuTable[i].setBorder(x, y - 1, 'thick', 'bottom')
        }
      }
    }

    // 描画
    for (let i = 0; i < noChikokuTable.length; i++) {
      noChikokuDoc.drawTable({
        tableData: noChikokuTable[i].field,
        start: {
          x: this.margin.left,
          y: this.margin.top + cell.height * (this.titleHeightAspect + this.gapAspect * (i + 2) + this.subTitleHeightAspect + cuttedTable.vertical * i)
        },
        end: {
          x: noChikokuDoc.doc.page.width - this.margin.right,
          y: this.margin.top + cell.height * (this.titleHeightAspect + this.gapAspect * (i + 2) + this.subTitleHeightAspect + cuttedTable.vertical * (i + 1))
        }
      })
    }

    // タイトルとサブタイトルを描画
    let title = {chr: `遅刻者チェック表${String(this.month)}月`}
    let subTitle = {chr: '□の中に遅刻者の数を記入してください。'}
    title.height = cell.height * this.titleHeightAspect
    subTitle.height = cell.height * this.subTitleHeightAspect
    title.start = {
      x: this.margin.left,
      y: this.margin.top
    }
    title.end = {
      x: noChikokuDoc.doc.page.width - this.margin.right,
      y: title.start.y + title.height
    }
    subTitle.start = {
      x: this.margin.left,
      y: this.margin.top + title.height + cell.height * this.gapAspect
    }
    subTitle.end = {
      x: noChikokuDoc.doc.page.width - this.margin.right,
      y: subTitle.start.y + subTitle.height
    }
    noChikokuDoc.drawText({
      text: title.chr,
      start: title.start,
      end: title.end,
      font: font
    })
    noChikokuDoc.drawText({
      text: subTitle.chr,
      start: subTitle.start,
      end: subTitle.end,
      font: font
    })
    // 表の情報を設定
    noChikokuDoc.doc.info.Title = `遅刻者チェック表 - ${String(this.year)}年${String(this.month)}月`
    noChikokuDoc.doc.info.Author = 'TAIYOSAMA - https://github.com/TAIYOSAMA'
    noChikokuDoc.doc.info.Subject = `遅刻者チェック表 - ${String(this.year)}年${String(this.month)}月`
    noChikokuDoc.doc.info.Keywords = '遅刻者チェック表'
    noChikokuDoc.doc.info.ModDate = noChikokuDoc.doc.info.CreationDate
    noChikokuDoc.end()
  }
}

function createNoChikoku ({
  year = 2024,
  month = 1,
  holidays = [1],
  grade = [3,3,3]
}) {
  // フォントの読み込み後、pageProgramを実行
  xhr.open('GET', './assets/fonts/font.ttf', true) //publicフォルダの中に存在します
  xhr.responseType = 'arraybuffer'
  xhr.onload = function(e) {
    const arrayBuffer = xhr.response
    const fontData = new Uint8Array(arrayBuffer)
    // 読み込んだフォントをもとに実行
    new NoChikoku({
      year: year,
      month: month,
      holidays: holidays,
      grade: grade
    }).draw(fontData)
  }
  xhr.send(null)
}

exports.generate = ({
  year = 2024,
  month = 1,
  holidays = [1,6,7,8,13,14,20,21,27,28],
  grade = [3,3,3]
}) => {
  createNoChikoku({
    year: year,
    month: month,
    holidays: holidays,
    grade: grade
  });
}
